@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700|Poppins:700");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  color: #111111;
  font-size: 16px;
  line-height: 1.6;
  font-family: 'Open Sans', sans-serif;
}

a {
  text-decoration: none;
  color: #111111;
  transition: all 200ms ease-in-out;
}

a:hover {
  color: #FF851B;
}

.flex {
  display: flex;
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.flex-space--between {
  justify-content: space-between;
}

.flex-space--around {
  justify-content: space-around;
}

.flex-align--center {
  align-items: center;
}

.flex-align--end {
  align-items: flex-end;
}

.flex-direction--column {
  flex-direction: column;
}

.flex-end {
  justify-content: flex-end;
}

.fr-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 0 15px;
}

.fr-portfolio {
  padding: 50px 0;
  text-align: center;
}

.fr-portfolio--thumb {
  width: 150px;
  height: 150px;
  margin: 0 auto 45px;
  background: #111111;
  background-size: cover;
  background-image: url(https://images.unsplash.com/photo-1509765731054-9e41a9aa31f1?ixlib=rb-0.3.5&s=39121bfa31058b01a9e92dadb68e4ae4&auto=format&fit=crop&w=634&q=80);
  border-radius: 50%;
}

.fr-portfolio--welcome {
  font-family: 'Poppins', 'Open Sans', sans-serif;
  font-weight: 700;
  font-size: 40px;
  margin-bottom: 15px;
}

.fr-portfolio--about {
  margin-bottom: 25px;
}

.fr-portfolio--list {
  flex-flow: wrap;
}

.fr-portfolio--social {
  margin-bottom: 25px;
}

.fr-portfolio--social li {
  margin: 0 15px;
}

.fr-portfolio--social li a svg {
  font-size: 25px;
}

.fr-portfolio--item {
  width: 32%;
  display: block;
  margin-bottom: 25px;
  height: 120px;
  background: #111111;
  background-size: cover;
}
